async function getAndPutNewOrders(e) {
  e.preventDefault();
  //     
  const jsonInputValue = refs.textareaPutJson.value;

  try {
    //     JSON
    const jsonData = JSON.parse(jsonInputValue);
    console.log(jsonData);
    console.log(createMarkupOrdres([jsonData]));

    //  fetchPostMyOrders   
    try {
      const updatedOrders = await fetchPostMyOrders(jsonData);
      console.log('    :', updatedOrders);
      successShow();

      //   debit   totalPrice
      try {
        const debit = await fetchMyDebit();
        const { amount } = debit;
        const {
          receipt: { totalPrice },
        } = jsonData;

        //  totalPrice  debit    
        const newAmount = amount - totalPrice;

        try {
          const updatedDebit = await fetchPutMyDebit('1', {
            amount: newAmount,
          });
          console.log('  :', updatedDebit);
          refs.amountDebit.textContent =
            updatedDebit.amount.toFixed(2) + ' EUR';
        } catch (error) {
          console.error('    :', error);
          failureShow();
        }
      } catch (error) {
        console.error('     :', error);
        failureShow();
      }
    } catch (error) {
      console.error('    :', error);
      failureShow();
    }
  } catch (error) {
    console.error('    JSON:', error);
    failureShow();
  }

  refs.textareaPutJson.value = '';
}

function failureShow() {
  Notify.failure('Oops, something went wrong.', {
    width: '220px',
    position: 'left-top',
    failure: {
      background: '#ff5549',
      textColor: '#fff',
      notiflixIconColor: '#fff',
    },
  });
}

function successShow() {
  Notify.success('Data sent and updated successfully.', {
    width: '220px',
    position: 'left-top',
    success: {
      background: '#5bc643',
      textColor: '#fff',
      notiflixIconColor: '#fff',
    },
  });
}
